import React from "react";
import { useState } from 'react';
import { useNavigate } from "react-router-dom";
import axios from 'axios';
// import "./ForgetPassword.css";
function ForgetPassword() {
    // const url = "http://localhost:3000/users/forgetpassword";

    const [data, setData] = useState({
        email: "",

    });
    const [error, setError] = useState("");
    const navigate = useNavigate();

    const handleChange = ({ currentTarget: input }) => {
        setData({ ...data, [input.name]: input.value });
    };
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const url = "http://localhost:3000/users/forgetpassword";
            const { data: res } = await axios.post(url, data);
            navigate("/Login");
            console.log(res.message);
        } catch (error) {
            if (
                error.response &&
                error.response.status >= 400 &&
                error.response.status <= 500
            ) {
                setError(error.response.data.message);
            }
        }
    };
    return (
        <div style={{ backgroundColor: "#6218e2" }}>
        <div className="container d-flex flex-column">
            <div
                className="row align-items-center justify-content-center min-vh-100"
            >
                <div className="col-12 col-md-8 col-lg-4">
                    <div className="card shadow-sm">
                        <div className="card-body">
                            <div className="mb-6">
                                <h5 className="mb-3 fs-4 text-center">Forgot Password?</h5>
                                <p className="mb-3 fs-5">
                                    Enter your registered email ID to reset the password
                                </p>
                            </div>
                            <form onSubmit={handleSubmit}>
                                <div className="mb-3">
                                    <label htmlFor="email" className="form-label mb-3 fs-5">
                                        Email
                                    </label>
                                    <input
                                        type="email"
                                        id="email"
                                        className="form-control "
                                        name="email"
                                        placeholder="Enter Your Email"
                                        required=""
                                        value={data.email}
                                        onChange={handleChange}
                                    />
                                </div>
                                <div className="mb-3 d-grid">
                                    <button type="submit" className="btn btn-primary fs-5">
                                        Reset Password
                                    </button>
                                </div>

                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </div>
    );
}
export default ForgetPassword;